
:host, input, .input-group-text, select, button {
    font-size: 12px !important;
}

.indexRow td {
    background: #d8f5ff;
    font-weight: bold;
}

.niftyRow td {
    background: #c3e4ef;
}

.cursor-pointer {
    cursor: pointer;
}

