.custom-popup-close {
    cursor: pointer;
    font-size: 18px;
    position: absolute;
    right: 5px;
    top: 0px;
}

.green {
    color: green;
}

.red {
    color: #ff6347;
}

.chart-stock {
    position: relative;
}

.chart-legend {
	position: absolute;
	left: 4px;
	top: 2px;
	z-index: 2;
	font-size: 18px;
	line-height: 28px;
	font-weight: 300;
    color: black;
}

.chart-popup-table {
    width: 120px;
    white-space: nowrap;
}

.watchlist-row td {
    background: #e1ebfe !important;
}

.notmatchingfilter-row td {
    background: lightgrey;
}

.stockChip {
    background: #f0f0f0;
    border-radius: 4px;
    display: inline-block;
    margin-right: 4px;
    padding: 0 4px;
}

.stockChipRed {
    background: #ff6347;
    color: white;
}

.stockChipGreen {
    background: #28a745; /* Green background */
    color: white; /* White text color for contrast */
}

.rank {
    color: black;
    font-size: 10px;
    margin-left: 5px;
}

.returns-value {
    width: 50px; 
    text-align: right; 
    display: inline-block;
}

.table thead {
    position: sticky;
    top: 0;
    z-index: 1020; /* Ensures the header stays on top */
    background: white; /* To avoid transparency issues */
}

.rankShortlisted {
    background: #c9eec9 !important;
}
